<template>
  <div id="app">
    <div class="mx-auto mt-4" style="width: 600px;">
      <h5 class="display-4">DEVELOPED BY IOWND</h5>
    </div>
    <b-container class="mt-4">
      <div>
        <b-button
          v-for="profesion of profesiones"
          class="m-1 button"
          @dragstart="dragStart"
          @dragover.stop
          @click="addLast(profesion)"
          :draggable="true"
          :id="profesion.id"
          :variant="profesion.type"
          :key="profesion.id"
          v-show="profesion.usable"
        >
          {{ `${profesion.name}(${profesion.available})` }}
        </b-button>

        <b-button v-b-modal.modal-1 variant="info" class="ml-2"
          >Agregar PJ</b-button
        >

        <b-modal
          id="modal-1"
          title="Nuevo Personaje"
          @ok="addPj"
          @cancel="newPjName = ''"
        >
          <b-input
            autofocus
            class="mb-2 mr-sm-2 mb-sm-0"
            placeholder="ENANO, CARDI, PONY, DUELISTA, ETC.."
            v-model="newPjName"
          ></b-input>
        </b-modal>
      </div>
    </b-container>

    <div class="mx-auto" style="width: 500px;">
      <b-container class="mt-5">
        <b-form inline @submit.prevent="addPlayer">
          <b-input
            id="name"
            autofocus
            class="mb-2 mr-sm-2 mb-sm-0"
            placeholder="Ingrese Jugador/Party"
            v-model="player"
            @keyup.enter="addPlayer"
          ></b-input>
          <b-button variant="primary" @click="addPlayer">Agregar</b-button>
          <b-button variant="danger" class="ml-1" @click="clean"
            >Borrar Todos</b-button
          >
        </b-form>
      </b-container>
    </div>

    <b-container fluid class="mt-5">
      <div v-show="showInitialMessage" class="mx-auto" style="width: 650px;">
        <p class="text-white">
          Agregue un Jugador/Party para empezar, luego arrastre los personajes
          para ir rellenado
        </p>
      </div>
      <b-row>
        <b-col sm="2" v-for="(player, index) in players" :key="index">
          <div>
            <b-card
              :title="player.name + '(' + player.goTo.length + ')'"
              style="max-width: 20rem; color: white; text-align:center;"
              class="mb-5 bg-secondary"
            >
              <hr />
              <b-button
                class="m-1"
                v-for="(go, index2) in player.goTo"
                :key="index2"
                :variant="go.type"
                @click="deleteGoTo(player.id, go)"
              >
                {{ go.name }}
              </b-button>
              <div
                class="drop-area pb-5"
                @dragover.prevent
                @drop.prevent="drop"
                :id="player.id"
              ></div>
            </b-card>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
const [TANK, BUFFER, DEBUFFER, DD, NEW_PJ] = [
  "warning ",
  "success",
  "dark",
  "danger",
  "info"
];
const AVAILABLE_INIT = 10;

export default {
  name: "App",
  mounted() {
    this.showInitialMessage = true;
  },
  data() {
    return {
      player: null,
      newPjName: null,
      onDrag: null,
      showInitialMessage: null,
      profesiones: [
        {
          id: 1,
          name: "TITAN",
          type: DD,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 2,
          name: "GK",
          type: DD,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 3,
          name: "DAGGER",
          type: DD,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 4,
          name: "CC",
          type: BUFFER,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 5,
          name: "COV",
          type: BUFFER,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 6,
          name: "APPETITE",
          type: BUFFER,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 7,
          name: "GATO",
          type: BUFFER,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 8,
          name: "SWM",
          type: BUFFER,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 9,
          name: "SD",
          type: BUFFER,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 10,
          name: "SEMI NEGRA",
          type: TANK,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 11,
          name: "SEMI BLANCA",
          type: TANK,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 12,
          name: "STIGMA",
          type: DEBUFFER,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 13,
          name: "NECRO",
          type: DEBUFFER,
          usable: true,
          available: AVAILABLE_INIT
        },
        {
          id: 14,
          name: "LIMIT",
          type: DEBUFFER,
          usable: true,
          available: AVAILABLE_INIT
        }
      ],
      players: []
    };
  },
  methods: {
    addPj() {
      const newProfesion = {
        type: NEW_PJ,
        available: AVAILABLE_INIT,
        name: this.newPjName.toUpperCase(),
        usable: true,
        id: Date.now()
      };
      this.profesiones.push(newProfesion);
      this.newPjName = "";
    },
    addLast(profesion) {
      const playersLength = this.players.length;
      if (!playersLength) return;
      this.players[playersLength - 1].goTo.push(profesion);
      profesion.available--;
      if (!profesion.available) profesion.usable = false;
    },
    addPlayer() {
      if (this.player === "") return;
      this.players.push({
        id: Date.now(),
        name: this.player.toUpperCase(),
        goTo: []
      });
      this.player = "";
      this.showInitialMessage = false;
    },
    clean() {
      this.players = [];
      for (const profesion of this.profesiones) {
        profesion.usable = true;
        profesion.available = AVAILABLE_INIT;
      }
    },
    drop(e) {
      const dropId = e.target.id;
      const profesionId = this.onDrag;
      const profesion = this.profesiones.find(
        element => element.id == profesionId
      );
      profesion.available--;
      if (!profesion.available) profesion.usable = false;
      this.players.find(user => user.id == dropId).goTo.push(profesion);
    },
    dragStart(e) {
      this.onDrag = e.target.id;
    },
    deleteGoTo(playerId, go) {
      const indexProfesionToDelete = this.players
        .find(player => player.id == playerId)
        .goTo.indexOf(go);

      this.players
        .find(player => player.id == playerId)
        .goTo.splice(indexProfesionToDelete, 1);
      this.profesiones.find(profesion => profesion.id == go.id).available++;
      this.profesiones.find(profesion => profesion.id == go.id).usable = true;
    }
  }
};
</script>

<style>
body {
  background: #2c3e50; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #4ca1af,
    #2c3e50
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #4ca1af,
    #2c3e50
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.drop-area {
  /* display: flex;
  flex-direction: column; */
  width: 100%;
  max-width: 900px;
  padding: 15px;
  height: 100%;
